<template>
  <div v-if="show" class="modal">
    <div class="modal-content">
      <div class="modal-close">
        <i class="material-icons" @click="closeModal">close</i>
      </div>
      <div class="modal-text">
        <h1>{{ name }}</h1>
        <span>Tareas realizadas:</span>
        <ul>
          <li v-for="task in tasks" :key="task.key">
            <i class="material-icons">check</i>
            <span>{{ task }}</span>
          </li>
        </ul>
      </div>
      <div class="modal-image" :style="'background-image: url(' + image + ')'">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    closeModal () {
      this.show = false
    }
  },
  name: 'modal',
  props: {
    image: String,
    name: String,
    show: Boolean,
    tasks: Array
  }
}
</script>

